import { useState } from "react";


const Accordion = ({ children, title, toggleAccodion }) => {
    
    const [accordionState, setState] = useState("");

    const toggleAccordion = () => {
        accordionState === 'open'? setState(''):setState('open');
        console.log(accordionState);
        return(accordionState);
    };


    return (
        <div className={toggleAccordion()} onClick={()=>toggleAccordion()}>
            <div className="accordion-head">
                <h1>{title}</h1>
            </div>
            {children}
        </div>
    );
    

}




export default Accordion;

